// Sauvegarde locale avec localStorage
const saveToLocalStorage = () => {
    localStorage.setItem("scenes", JSON.stringify(scenes));
    localStorage.setItem("transitions", JSON.stringify(transitions));
    alert("Sauvegarde locale réussie !");
  };
  
  // Sauvegarde en ligne sur FastAPI
  const saveToBackend = async () => {
    try {
      await fetch("http://127.0.0.1:8000/show", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ scenes, transitions }),
      });
      alert("Sauvegarde en ligne réussie !");
    } catch (error) {
      console.error("Erreur lors de la sauvegarde en ligne :", error);
    }
  };
  
  // Chargement local depuis localStorage
  const loadFromLocalStorage = () => {
    const savedScenes = localStorage.getItem("scenes");
    const savedTransitions = localStorage.getItem("transitions");
  
    if (savedScenes && savedTransitions) {
      setScenes(JSON.parse(savedScenes));
      setTransitions(JSON.parse(savedTransitions));
      alert("Chargement local réussi !");
    } else {
      alert("Aucune sauvegarde locale trouvée.");
    }
  };
  
  // Chargement en ligne depuis FastAPI
  const loadFromBackend = async () => {
    try {
      const response = await fetch("http://127.0.0.1:8000/show");
      const data = await response.json();
      setScenes(data.scenes);
      setTransitions(data.transitions);
      alert("Chargement en ligne réussi !");
    } catch (error) {
      console.error("Erreur lors du chargement en ligne :", error);
    }
  };
  